.main {
    display: grid;
    width: 100vw;
    height: 1655px;
    color: white;

}
.background2{
    background-image: url('data:image/jpeg;base64,');
    height: 1550px;
    width:1200px;
    z-index: 900;
    margin-left: 118px;
    /* border: red 10px solid; */
    position: absolute;
    top: 105px;
    border-left: 14px solid #333333;
    border-right: 14px solid #333333;
        
}
.header {
    width: 100vw;
    background-color: #333333;
    height: 105px;
    display: grid;
    grid-template-columns: 1fr 2fr 1fr 1fr;
    justify-items: center;
    -ms-flex-align: center;
        align-items: center;
    position: fixed;
    z-index: 1000;
}
.cartHolder{
    width: 45%;
    color: skyblue;


}
.cartIcon {
  width: 45%;
}
.prodPic {
height: 250px;
width: auto;
background-color: white;
top: 0;
justify-self: center;
/* border: 5px red solid; */

}
.userName {
    height: 60px;
    width: 170px;
    color:  skyblue;
    justify-self: start;
    margin-left: 20px;
    letter-spacing: 2px;
    text-transform: uppercase;
    line-height: 1.2; 
    
}
.prodGrid{
    display: grid;
    grid-template-columns: 2fr 2fr 2fr;
    grid-template-rows: 1fr 1fr 1fr;
    justify-items: center;
    margin-left: 90px;
    margin-top: 35px;
    grid-column-gap: 5%;
    grid-row-gap: 35px;
    /* background-color: #4c4c4c; */
    /* border: 3px solid skyblue; */
    width: 80%;
    height: 85%;
}
.logoutButton {
    /* background-color: slategray; */
    
}
.products{
     background-color: rgba(0, 0, 0, 0.582);
     color: white;
     border: 3px solid black;
     text-transform: capitalize;
     line-height: 1.5;
     margin-left: 0%;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-line-pack: center;
        align-content: center;
     -webkit-box-shadow: 9px 9px 6px rgba(0, 0, 0, 0.623);
             box-shadow: 9px 9px 6px rgba(0, 0, 0, 0.623);
     border-radius: 4px;
     display: grid;
     grid-template-columns: 1fr;
     text-align: center;
     -ms-flex-pack: center;
         justify-content: center;
     /* justify-items: center; */
     height: 400px;
     width: 320px;
     /* grid-template-rows: 1fr 1fr 1fr; */
     
     /* margin-top: 10%; */
}
.cartThing:hover{
    /* color: hotpink; */
    -webkit-filter: hue-rotate(90deg); /* Safari */
    filter: hue-rotate(90deg);
}
.addToCart{
    margin-left: 4%;
}
.headTitle{
    height: 90px;
    width: 450px;
    font-size: 32px;
    color: white;
    margin-top: 24px;
    letter-spacing: 2px;
}
.thanks {
    font-size: 18px;
    margin-left: 0px;
    margin-top: 0px;
    color: white;
    letter-spacing: 1.5px;
    background-color: #333333;
    width:  100%;
    height: 40px;
    text-align: center;
    border-top: 20px solid #333333;
   
}
.bottom{
    margin-left: 10px;
}








@media (max-width: 1100px) {
    .logoutButton{ 
    background-color: slategray;
   }
   .prodGrid {
    grid-template-columns: 1fr 1fr;
    margin-left: 2.5%;
    }
    .prodPic{
        height: 280px;
    }
    .main {
        min-height: 150vh;
    }
    .header{
        grid-template-columns: 1fr 2fr 1fr 1fr;
        grid-template-rows: 1fr;
    }
    .cartIcon{
        height: 6vh;
        width: auto;

    }
    button {
        height: 4vh;
        width: auto;
        font-size: 1.5rem;
    }
  
    
}
@media (max-width: 1020px){
    .prodGrid {
        margin-left: 4%;
    }
}


@media (max-width: 740px){
    .prodPic{
        height: 18vh;
        width: auto;
    }
    .prodGrid{
        margin-left: 25%;
        grid-template-columns: 1fr;
    }
    .header{
        grid-template-columns: 1fr 1fr;
    }
    .cartIcon{
        width: 60%;
        height: 60%;
    }
    .body{
        position: absolute;
        top: 19vh;

    }
    .userName {
        letter-spacing: 1px;
        font-size: 9px; 
    }
}
@media (max-width: 395px){
    .prodGrid {
        width: 60%;

    }
    .prodPic{
        height: 140px;
    }
    .cartIcon {
        height: 6.5vh;
        width: auto;
    }
}



