.mainCheckout {
    display: grid;
    width: 100vw;
    height: 826px;
    color: white;
    background-color: rgb(101, 211, 255);
    display: grid;
    /* position: absolute; */
  
}
.background2Checkout{
    background-image: url('data:image/jpeg;base64,');
    height: 721px;
    width:1000px;
    /* z-index: 900; */
    margin-left: 158px;
    /* border: red 10px solid; */
    position: absolute;
    top: 105px;
    border-left: 14px solid #333333;
    border-right: 14px solid #333333;
    background-size: auto;
    display: grid;  
    grid-template-columns: 240px 240px 240px;
        
}
.userName {
    height: 60px;
    width: 170px;
    color:  skyblue;
    justify-self: start;
    margin-left: 20px;
    letter-spacing: 2px;
    text-transform: uppercase;
    line-height: 1.2; 
    
}
.cartThing:hover{
    /* color: hotpink; */
    -webkit-filter: hue-rotate(90deg); /* Safari */
    filter: hue-rotate(90deg);
}
.cartHolder{
    width: 45%;
    color: skyblue;


}
.cartIcon {
  width: 45%;
}
.headTitle{
    height: 90px;
    width: 450px;
    font-size: 32px;
    color: white;
    margin-top: 24px;
    letter-spacing: 2px;
}
.stripe {
    height: 30px;
    grid-column-start: 1;
    grid-column-end: 5;
    width: 150px;
    margin-left: 380px;
}
.checkoutContainer{
    background-color: rgba(0, 0, 0, 0.582);
    color: white;
    border: 3px solid black;
    text-transform: capitalize;
    /* align-content: center; */
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-shadow: 9px 9px 6px rgba(0, 0, 0, 0.623);
            box-shadow: 9px 9px 6px rgba(0, 0, 0, 0.623);
    border-radius: 4px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    -ms-flex-pack: center;
        justify-content: center;
    height: 300px;
    width: 900px;
    margin-left: 45px;
    margin-top: 40px;
    font-size: 24px;
    text-align: center
}
.checkoutPrice {
    grid-column-start: 1;
    grid-column-end: 5;
    margin-top: 15px;
    grid-column-start: 1;
}
.card {
    grid-column-start: 1;
    grid-column-end: 5;
    grid-row-start: 2;
}
.buttons {
    margin-left: 140px;
}
.buttons2 {
    margin-left: 130px;
    width: 175px;
}



