.mainCart {
    display: grid;
    width: 100vw;
    height: 1575px;
    color: white;
    background-color: rgb(101, 211, 255);
    display: grid;

}
.background2Cart{
    background-image: url('data:image/jpeg;base64,');
    height: 1470px;
    width:1000px;
    z-index: 900;
    margin-left: 158px;
    /* border: red 10px solid; */
    position: absolute;
    top: 105px;
    border-left: 14px solid #333333;
    border-right: 14px solid #333333;
    background-size: auto;
    /* display: grid;   */
    /* grid-template-columns: 240px 240px 240px; */
    text-align: center;
        
}
.userName {
    height: 60px;
    width: 170px;
    color:  skyblue;
    justify-self: start;
    margin-left: 20px;
    letter-spacing: 2px;
    text-transform: uppercase;
    line-height: 1.2; 
    
}
.cartThing:hover{
    /* color: hotpink; */
    -webkit-filter: hue-rotate(90deg); /* Safari */
    filter: hue-rotate(90deg);
}
.cartHolder{
    width: 45%;
    color: skyblue;


}
.cartIcon {
  width: 45%;
}
.headTitle{
    height: 90px;
    width: 450px;
    font-size: 32px;
    color: white;
    margin-top: 24px;
    letter-spacing: 2px;
}
.cartProducts {
    /* display: grid;
    grid-template-columns: 340px 340px 340px; */
    height: 100px;
    width: auto;
    grid-row-gap: 22px;
    grid-column-gap: 15px;
    margin-left: 160px;
    font-size: 20px;
    letter-spacing: 1px;
}
.bodyCart {
    display: grid;
}
