    height: 10px;
    width: auto;
} */
.header {
    width: 100vw;
    background-color: #333333;
    height: 105px;
    display: grid;
    grid-template-columns: 1fr 2fr 1fr 1fr;
    justify-items: center;
    -ms-flex-align: center;
        align-items: center;
    position: fixed;
    z-index: 1000;
}
.prodPicCart {
    height: 250px;
    width: auto;
    background-color: white;
    top: 0;
    justify-self: center;
    /* border: 5px red solid; */
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    
    
    }
    .prodGridCart{
        /* display: grid;
        grid-template-columns: 2fr 2fr 2fr; */
        /* grid-template-rows: 1fr 1fr 1fr; */
        justify-items: center;
        margin-left: 90px;
        margin-top: 35px;
        grid-column-gap: 5%;
        grid-row-gap: 35px;
        /* background-color: #4c4c4c; */
        /* border: 3px solid skyblue; */
        width: 80%;
        height: 85%;
    }
    .productsCart{
        background-color: rgba(0, 0, 0, 0.582);
        color: white;
        border: 3px solid black;
        text-transform: capitalize;
        line-height: 1.5;
        margin-left: 0%;
        -ms-flex-align: center;
            align-items: center;
        -ms-flex-line-pack: center;
            align-content: center;
        -webkit-box-shadow: 9px 9px 6px rgba(0, 0, 0, 0.623);
                box-shadow: 9px 9px 6px rgba(0, 0, 0, 0.623);
        border-radius: 4px;
        /* display: grid;
        grid-template-columns: 1fr 1fr 1fr; */
        text-align: center;
        -ms-flex-pack: center;
            justify-content: center;
        justify-items: center;
        height: 380px;
        width: 250px;
        font-size: 19px;
        /* grid-template-rows: 1fr 1fr 1fr; */
        
        /* margin-top: 10%; */
   }
.cartGrid {
    display: grid;
    grid-template-columns: 340px 340px 340px;
    margin-left: 15px; 
    margin-top: 0px;
    grid-row-gap: 20px;
}
.cartPhoto {
    margin-top: 15px;
}
.subTotal {
    font-size: 24px;
    -webkit-box-shadow: 9px 9px 6px rgba(0, 0, 0, 0.623);
            box-shadow: 9px 9px 6px rgba(0, 0, 0, 0.623);
    border-radius: 4px;
    background-color: rgba(0, 0, 0, 0.582);
    color: white;
    border: 3px solid black;
    width: 450px;
    height: 140px;
    margin-left: 290px;
    margin-bottom: 30px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin-top: 35px;
}
.price {
    grid-column-start: 1;
    grid-column-end: 4;
    margin-top: 15px;
}



